.main {
    display: flex;
    flex-direction: column;
    width: 234px;
    position: relative;
    transition-duration: .3s;
    padding: 15px;
    box-sizing: border-box;
    border: 1px solid transparent;
    border-radius: 8px;
    cursor: pointer;
    height: fit-content;
}
.main:hover {
    background-color: white;
    border: 1px solid #9DC8CF;
}
.image {
    width: 100%;
    height: 180px;
    object-fit: cover;
    transition-duration: .3s;
}
.info {
    width: 100%;
    display: flex;
    justify-content: space-between;
    gap: 10px;
    margin-top: 10px;
}
.left {
    width: 60%;
}
.left p {
    text-overflow: ellipsis;
    overflow: hidden; 
    width: 100%;
    white-space: nowrap;
    margin-bottom: 7px;
    color: #3B3F61;
    font-weight: 700;
    font-size: 14px;
}
.left p::first-letter {
    text-transform: capitalize;
}
.price {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}
.price p {
    color: #E86589;
    font-weight: 700;
    font-size: 14px;
    text-overflow: ellipsis;
    overflow: hidden; 
    white-space: nowrap;
    width: 100%;
}
.inAction {
    text-decoration: line-through;
    font-size: 12px !important;
    color: #3B3F61 !important;
    margin-top: 2px;
}
.discount {
    font-weight: 700;
    font-size: 14px;
    color: #E86589;
}
.buyBut {
    font-family: 'Montserrat' !important;
    width: 55% !important;
    border-radius: 15px !important;
    background-color: transparent !important;
    border: 1px solid #9DC8CF !important;
    color: #9DC8CF !important;
    font-size: 14px !important;
    font-weight: 700 !important;
    height: 32px !important;
    margin-top: 21px !important;
}
.buyBut:hover {
    background-color: #9DC8CF !important;
    color: white !important;
}
.baiges {
    position: absolute;
    right: -15px;
    top: 25px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    gap: 4px;
}

@media screen and (max-width: 1170px) {
    .main {
        width: 230px;
        max-width: 100%;
    }
}

@media screen and (max-width: 468px) {
    .main {
        padding: 0;
    }
    .buyBut {
        background-color: #9DC8CF !important;
        color: white !important;
    }
}